import {Link} from 'react-router-dom';
import styled from 'styled-components';
import close from '../images/close.svg';
import warning from '../images/warning.svg';

const Cov= styled.div`
  border:solid 5px ${props=>props.theme.orange1};
  min-width:300px;
  max-width:500px;
  box-sizing:border-box;
  background:${props=>props.theme.blue6};
  position:absolute;
  padding:20px;
  top:80px;
  left:50%;
  transform:translate(-50%);
  z-index:30;

  >header{
    color:${props=>props.theme.blue1};
    font-size:20px;
    margin-right:45px;
  }

  >ul{

    li {
      font-size:17px;
      color:${props=>props.theme.orange3}
    }
  }

  >div:last-child{
    p {
      color:black;
      font-family:${props=>props.theme.seriffont};
      margin:10px 0;
    }
  }

  #x-close {
    position:absolute;
    right:0;
    top:0;
    margin:10px 10px 0 0;
    cursor:pointer;
  }


`;


function Covid(props) {


  function closeCovid(){
    document.getElementById("covid").style.display="none";
  }

  return (
    <Cov id="covid">
      <img id="x-close" onClick={closeCovid}src={close}/>
      <header><img src={warning}/>The Color Blue's Covid-19 Statement</header>
      <ul>
        <li>We comply with all federal, state, and local health guidelines.</li>
        <li>We support and listen to the hardworking medical professionals who are risking their lives for everyone.</li>
      </ul>
      <div>
        <p>
          These are crucial times. The entire world is facing an
          unprecedented crisis.
        </p>
        <p>
          The rules are also constantly changing.
        </p>
        <p>
          At The Color Blue, we take every necessary precaution to ensure our
          clients are safe.
        </p>
        <p>
          For this reason, we are conducting all consultations outside of Chicago
          over the phone, or in Zoom video conferences.
        </p>
      </div>
    </Cov>

  );
}

export default Covid;
