import styled from 'styled-components';
import Pageheadingsub from '../components/Pageheadingsub.js';
import Pageheadingmain from '../components/Pageheadingmain.js';
import {HashRouter as Router, Link} from 'react-router-dom';
import existing1 from '../images/existing1.svg';
import existing2 from '../images/existing2.svg';
import existing3 from '../images/existing3.svg';
import existing4 from '../images/existing4.svg';
import truck from '../images/truck.svg';
import {useEffect} from 'react';

const Ex = styled.div`
  display:grid;
  grid-template-rows:repeat(auto-fill,auto);
  grid-row-gap:40px;
  >section:first-child{
    text-align:center;
    >div {
      margin-bottom:5px;
    }
    >a, &:visited {
      color:${props=>props.theme.orange1};
      font-weight:bold;

    }
  }

  >section:nth-child(2) {
    display:grid;
    margin:auto;
    grid-template-columns:repeat(2,auto);
    grid-template-rows:repeat(2,auto);
    grid-gap:40px;
    max-width:1100px;

    @media screen and (max-width: 768px) {
      grid-template-columns:1fr;
      grid-template-rows:repeat(auto-fill,auto);
    }

    >div {
      display:flex;
      flex-direction:column;
      justify-content:flex-start;
      align-items:center;

      >header {
        font-size:20px;
        font-weight:bold;
      }

      >p{
        max-width:410px;
        min-height:105px;
        margin:0;
        margin-top:10px;
        text-align:center;
      }

      >img{
        width:270px
      }
    }
  }

  >section:nth-child(3) {
    display:grid;
    grid-template-columns:1fr 1fr;
    grid-template-rows:auto auto;
    justify-items:center;

    grid-row-gap:30px;

    @media screen and (max-width: 840px) {
      grid-template-columns:auto;
      grid-template-rows:auto auto auto;
    }

    >div:first-child {
      max-width:570px;
      max-height:auto;
      >h2, >header, >div {
        text-align:center;
      }

      >header {
        font-size:20px;
        font-weight:bold;
        color:${props=>props.theme.black2};
      }
      >ul {
        padding-left:15px;
        >li {
          list-style-type:'-';
          margin-bottom:5px;

        }
      }

    }

    >div:nth-child(2) {
      width:290px;
      display:flex;
      flex-direction:column;
      align-items:center;

      >header {
        font-size:24px;
        font-weight:bold;
        color:${props=>props.theme.blue2}
      }

      >span {
        font-size:20px;
        font-weight:bold;
        text-align:center;
      }

      >a, &:visited {
        color:${props=>props.theme.orange1};
        font-weight:bold;
        margin:5px 0 10px;
      }
    }

    >div:last-child{
      grid-column:1/3;
      font-family:${props=>props.theme.seriffont};
      font-weight:bold;
      @media screen and (max-width: 840px) {
        grid-column:1/2;
      }

    }


  }



`;



function Existing () {
  useEffect(()=>window.scrollTo(0,0),[])
  return (
    <Ex>
      <section>
        <Pageheadingmain content={"Have a Website Already?"}/>
        <Pageheadingsub content={"We Can Make Any Changes You Like"}/>
        <Router>
          <Link to="/customizedsolutions">Request a consultation today!</Link>
        </Router>
      </section>



      <section>
        <div>
          <header>Inventory</header>
          <p>
              The products on your site should reflect availability. We can set up a
              solution that tracks and updates inventory on your website. Remove old
              items or add new ones that have just arrived.
          </p>
          <img src={existing1}/>
        </div>

        <div>
          <header>Add/Remove Pages</header>
          <p>
            As your business grows, the need will eventually arise to add sections and/or
            pages to your site. We make sure those new pages and sections flow seemlessly
            with the rest of your website.
          </p>
          <img src={existing2}/>
        </div>

        <div>
          <header>Style/Color Changes</header>
          <p>
            Keep your audience engaged. Refresh your buttons, forms, drop-down menus, and other components by giving them different styles or colors.
          </p>
          <img src={existing3}/>
        </div>

        <div>
          <header>Add/Remove Content/Images</header>
          <p>
            You may want to propagate your new catchy slogan throughout your entire site. You may want to add pictures of your new location or renovation.
            No matter what, let us take care of the hard work.
          </p>
          <img src={existing4}/>
        </div>
      </section>

      <section>
        <div>
          <div>
            <Pageheadingmain content={"5 Reasons for Change"}/>
            <Pageheadingsub content={"We Can Make Any Changes You Like"}/>
          </div>
          <header>Why would you want to do a complete remodel?</header>
          <ul>
            <li>Your current website was made with colors and design trends that were popular years ago.</li>
            <li>You've added sections or pages to your site on random occasions and now the website has no consistency.</li>
            <li>Your website was made with outdated technology, rendering much of your site useless.</li>
            <li>You recently renovated your physical business, and want your website to reflect the new theme.</li>
            <li>You are tired of seeing the same website and just want something new.</li>
          </ul>
        </div>

        <div>
          <header>Do it the Blue Way!</header>
          <span>Get the Most Out of Your Money When You Let Us Do A Complete Demolition and Rebuild</span>
          <Router><Link to="/customizedsolutions">Request a consultation today!</Link></Router>
          <img src={truck}/>
        </div>

        <div>
          <p>Website remodels come in different shapes and sizes. That's why we make it easier for you to know what to expect.
            You might want to keep some aspects of your website and rebuild everything else. No matter what your needs are, our
            engineers will work with you to make sure you don't have to settle for less!
          </p>
        </div>
      </section>
    </Ex>
  );
}

export default Existing;
