import styled from 'styled-components';
import name from '../images/name.svg';
import mail from '../images/mail.svg';
import business from '../images/business.svg';


const Inp=styled.div`

  >form {
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:center;
    width:100%;


    * {
      box-sizing:border-box;
      width:100%;
      background:none;
    }
    >select {
      margin-bottom:10px;
      border-radius:10px;
      border-color:${props=>props.theme.blue3};
      padding:6px;
      background:${props=>props.theme.blue6};
      font-weight:bold;
      color:${props=>props.theme.blue3};


      >option {
        font-weight:bold;
        color:black;



      }
    }

    >textarea {
      margin-bottom:10px;
      border-radius:10px;
      background:${props=>props.theme.blue6};
      font-weight:bold;
      border-color:${props=>props.theme.blue3};
      &::placeholder {
        color:${props=>props.theme.blue3};
        font-weight:bold;
      }

      &:focus {
        background:${props=>props.theme.orange4};
      }
    }



    >div {
      height:30px;
      border-radius:10px;
      padding:5px;
      border:solid 1px ${props=>props.theme.blue3};;
      margin-bottom:10px;
      background:${props=>props.theme.blue6};


      >img{
        box-sizing:content-box;
        width:20px;
        margin-right:10px;
        border-right:solid 2px ${props=>props.theme.blue3};
        padding:0 10px;
      }



      >input {
        color:black;
        font-weight:bold;
        border:none;


        &:focus {
          background:${props=>props.theme.orange4};

          outline:none;
        }

        &::placeholder {
          color:${props=>props.theme.blue3};
          font-weight:bold;
        }

      }

    }

    >div:nth-child(1), >div:nth-child(2), >div:nth-child(3) {
      display:flex;
      justify-content:flex-start;
    }

    >button {
      background:${props=>props.theme.orange2};
      width:100px;
      height:30px;
      color:white;
      font-weight:bold;
      border:none;
      box-shadow:1px 1px 2px black;
      &:active {
        box-shadow:none;
        outline:none;
      }
    }


  }


`;


function Inputform() {
  return (
    <Inp>
      <header></header>
      <form>
        <div><img src={name}/><input type="text" placeholder="Full Name"/></div>
        <div><img src={mail}/><input type="email" placeholder="E-mail Address"/></div>
        <div><img src={business}/><input type="text" placeholder="Business Name(Optional)"/></div>
        <select placeholder="asd">
          <option>Reason for Inquiry</option>
          <option>General Inquiry</option>
          <option>New Website</option>
          <option>Website Remodel</option>
          <option>I dont Know</option>
          <option>Other</option>
        </select>
        <textarea rows="10" placeholder="Please be detailed..."/>
        <button>Message</button>
      </form>
    </Inp>

  );
}

export default Inputform;
