import styled from 'styled-components';



const Spin= styled.div`

  height:240px;
  width:240px;
  .parent {
    width:240px;
    background:none;
    position:relative;
    color:white;
  }

  .spinner-box {
    position:relative;
    border-radius:50%;
    background:none;
    width:240px;
    height:240px;
    display:flex;
    align-items:center;
    justify-content: center;
    animation:spinning 18s linear infinite;
    mix-blend-mode:color;
  }

  @keyframes spinning {
    0% {transform:rotateZ(0deg);}
    12.5% {transform:rotateZ(90deg);}
    25% {transform:rotateZ(90deg);}
    37.5% {transform:rotateZ(180deg);}
    50% {transform:rotateZ(180deg);}
    62.5% {transform:rotateZ(270deg);}
    75% {transform:rotateZ(270deg);}
    87.5% {transform:rotateZ(360deg);}
    100% {transform:rotateZ(360deg);}
  }

  .circle-text-box {
    font-family:Segoe UI;
    font-weight:bold;
  }

  .circle-text-box:nth-child(1){
    left:50%;
    color:white;
    transform:translateX(-50%);
    background:none;
    position:absolute;
    top:33px;
  }

  .circle-text-box:nth-child(2){
    left:50%;
    color:white;
    transform:translateX(-50%);
    background:none;
    position:absolute;
    bottom:37px;
  }

  .circle-text-box:nth-child(3){
    left:16px;
    color:white;
    transform:translateY(-50%);
    background:none;
    position:absolute;
    top:50%;
  }

  .circle-text-box:nth-child(4){
    right:19px;
    color:white;
    transform:translateY(-50%);
    background:none;
    position:absolute;
    top:50%;
  }


  .spinner:nth-child(odd) {
    background:${props=>props.theme.blue1};
    height:85px;
    width:85px;
    border-radius:50%;
  }

  .spinner:nth-child(2) {
    background:${props=>props.theme.orange2};
    height:65px;
    width:65px;
    border-radius:50%;
  }

`;






function Spinner () {

  return (
    <Spin>
      <div class="parent">
        <div class="circle-text-box">Custom</div>
        <div class="circle-text-box">Solutions</div>
        <div class="circle-text-box">Modern</div>
        <div class="circle-text-box">Design</div>
        <div class="spinner-box">
          <div class="spinner"></div>
          <div class="spinner"></div>
          <div class="spinner"></div>
        </div>
      </div>
    </Spin>
  );

}
export default Spinner;
