import styled from 'styled-components';
import {useState, useEffect} from 'react';
import {useSelector,useDispatch} from 'react-redux';
import Pageheadingmain from'../components/Pageheadingmain.js';
import Pageheadingsub from'../components/Pageheadingsub.js';
import Planpackage from '../components/Planpackage.js';
import Servicequestion from '../components/Servicequestion.js';
import Updatequestion from '../components/Updatequestion.js';
import Cartbox from '../components/Cartbox.js';


const Plan=styled.div`

  display:grid;
  grid-template-rows:repeat(auto-fit,1fr);
  grid-row-gap:40px;
  >section:first-child {
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:flex-start;

    >div {
      @media screen and (max-width: 680px) {
        display:none;
      }
    }

  }


  >section:nth-child(2) {
    max-width:1150px;
    justify-self:center;
    >div {


      display:flex;
      justify-content:center;
      flex-wrap:wrap;

      >div {
        width:230px;
        margin:10px;
        padding:10px;
        text-align:center;
        color:${props=>props.theme.black2};
        >header {
          font-size:20px;
          font-weight:bold;
        }
        >p {
          font-size:16px;
          margin:5px;
        }
      }


    }

    >header {
      color:${props=>props.theme.blue1};
      font-size:24px;
      font-weight:bold;
      text-align:center;
    }
  }


  >section:nth-child(3) {
    display:grid;
    grid-template-rows:repeat(auto-fill, 1fr);
    grid-row-gap:30px;

    .headtitle {
      display:flex;
      flex-direction:column;
      align-items:center;
      font-size:24px;
      font-weight:bold;
      text-align:center;
      margin-bottom:20px;

      @media screen and (max-width: 768px) {
        font-size:20px;
      }
    }

    >div:first-child {
      display:flex;
      flex-direction:column;
      align-items:center;



      >div:last-child {

        display:flex;
        justify-content:center;
        flex-wrap:wrap;
        max-width:1200px;
        >div {
          margin:10px 20px;

          @media screen and (max-width: 500px) {
            margin:10px 0;
          }
        }
      }
    }
  }
`;


function Plans () {
  useEffect(()=>window.scrollTo(0,0),[])
  const form=useSelector(state=>state.form);
  const dispatch= useDispatch();


  const [mode,toggleMode]=useState(["light","light","light"]);

  function changeOne() {
    toggleMode((last)=>["dark","light","light"])
    dispatch({type:'size-small'})
  }

  function changeTwo() {
    toggleMode((last)=>["light", "dark", "light"])
    dispatch({type:'size-medium'})
  }

  function changeThree() {
    toggleMode((last)=>["light","light", "dark"])
    dispatch({type:'size-large'})
  }








  return (
    <Plan>
      <section>
        <Pageheadingmain content={"Plans & Pricing"}/>
        <Pageheadingsub content={"Made for People/Businesses with a Straightforward Online Presence"}/>
        <Pageheadingsub content={"If You Have Specific Needs, Discuss the Solutions We Have Just for You!"}/>
      </section>
      <section>
        <header>All Plans Include</header>
        <div>
          <div><header>Modern Technology</header><p>All of our client websites are made with the newest, fastest, most efficient technologies. Your website will look like it.</p></div>
          <div><header>Continued Maintenance</header><p>Your website is tested on a monthly basis to make sure things are running smoothly. We won't leave you hanging.</p></div>
          <div><header>Fully Customized</header><p>We don't use pre-made templates to make your website. Everything is custom-tailored for your brand and image.</p></div>
          <div><header>Direct Contact</header><p>You don't have to jump through hoops to get in touch with someone who knows your particular situation.</p></div>
        </div>
      </section>


      <section>
        <div>
          <div className="headtitle">
            <span>1</span>
            <header>Choose the Best Size for your Website</header>
          </div>
          <div>
            <div onClick={changeOne}><Planpackage mode={mode[0]} head={"Small"} pages={[1, 5]} hosting={60} price={50}/></div>
            <div onClick={changeTwo}><Planpackage mode={mode[1]} head={"Medium"} pages={[6, 15]} hosting={40} price={40}/></div>
            <div onClick={changeThree}><Planpackage mode={mode[2]} head={"Large"} pages={[16, 30]} hosting={25} price={25}/></div>
          </div>
        </div>



        <div>
          <div className="headtitle">
            <span>2</span>
            <header>Will You Sell Products and/or Services on Your Site?</header>
          </div>
          <Servicequestion/>
        </div>



        <div>
          <div className="headtitle">
            <span>3</span>
            <header>How Often Will You Need to Update Your Site?</header>
          </div>
          <Updatequestion/>
        </div>


        <div>
          <Cartbox/>
        </div>
      </section>

    </Plan>
  );

}


export default Plans;
