import styled from 'styled-components';

const Pagehead = styled.h2`
  color:${props=>props.theme.blue2};
  font-size:30px;
  line-height:30px;
  margin:0;
  font-weight:normal;
  font-family: 'Fredoka One', cursive;
`;



function Pageheadingmain(props) {

  return (
    <Pagehead>
      {props.content}
    </Pagehead>
  );
}

export default Pageheadingmain;
