import rocketlaunch2 from '../images/rocketlaunch2.svg';
import rocketlaunch3 from '../images/rocketlaunch3.svg';
import rocketlaunch from '../images/rocketlaunch.svg';
import hometile1 from '../images/hometile1.svg';
import hometile2 from '../images/hometile2.svg';
import hometile3 from '../images/hometile3.svg';
import hometile4 from '../images/hometile4.svg';
import Pageheadingmain from '../components/Pageheadingmain.js';
import Pageheadingsub from '../components/Pageheadingsub.js';
import Button from '../components/Button.js';
import styled from 'styled-components';
import Hometile from '../components/Hometile.js';
import {useEffect} from 'react';

const Homestyle= styled.div`
  >section:first-child {
    min-height:100vh;
    background-image:url(${rocketlaunch2});
    background-position:top;
    background-size:cover;
    background-repeat:no-repeat;

    >div:first-child {
      display:grid;
      grid-template-columns:repeat(auto-fit,minmax(240px,1fr));
      grid-column-gap:30%;
      margin:0 40px;

        >div {
          border:solid 1px white;

          >button {
            margin-top:25px;
          }

          >div:nth-child(2){
            margin-top:5px;
          }
        }

        >div:first-child {
          justify-self:center;
          margin-top:5vw;
        }

        >div:last-child {
          justify-self:center;
          margin-top:5vw;
        }
      }

  }


  >section:nth-child(2){

    margin:40px 0px 0;

    .tile-div{
      display:flex;
      justify-content:center;
      flex-wrap:wrap;

    }

  }

  @media screen and (max-width : 885px) {
    >section:first-child {
      position:relative;
      min-height:200px;
      background-image:url(${rocketlaunch});
      background-size:100%;
      padding-top:57%;
      padding-bottom:40px;
      >div:first-child{
        grid-template-columns:1fr;
        grid-row-gap:30px;
        margin:0 0px;


        h2 {
          text-align:center;
        }
        >div:first-child{
          display:flex;
          flex-direction:column;
          align-items:center;
          justify-self:center;
          >div:nth-child(2){
            text-align:center;
          }

        }

        >div:last-child{
          display:flex;
          align-items:center;
          flex-direction:column;
          justify-self:center;
        }
      }
    }

    >section:nth-child(2){
      margin:0 0px;
      .tile-div {

      }

    }
}
`;



function Home() {
  useEffect(()=>window.scrollTo(0,0))
  return (
    <Homestyle>
      <section>

      <div>
        <div>
          <Pageheadingmain content="Launch Your Website with Confidence"/>
          <Pageheadingsub content="Options for Every Industry"/>
          <Button content="See Our Plans" link={"./customizedsolutions"}/>
        </div>
        <div>
          <Pageheadingmain content="Your Online Presence is One Consultation Away"/>

          <Button content="Request Consultation" link={"./customizedsolutions"}/>
        </div>
      </div>
      </section>
      <section>
      <div className="tile-div">
        <Hometile head="Responsive Web Design" paragraph="Our engineers will work to make certain that each and every screen size is accounted for." picture={hometile1} />
        <Hometile head="Affordable Cost" paragraph="We're not in the business of hidden costs, and we operate with utmost transparency." picture={hometile2} />
        <Hometile head="24-hr Assistance" paragraph="Our staff is always available to chat and answer any questions you may have." picture={hometile3} />
        <Hometile head="Fast Turnaround Time" paragraph="With us, no need to wait on obscure deadlines. After all, your business shouldn't have to wait." picture={hometile4} />
      </div>
      </section>
    </Homestyle>

  );
}

export default Home;
