import react from 'react';
import Mainheader from './components/Mainheader.js';
import Mainfooter from './components/Mainfooter.js';
import {HashRouter as Router, Route, Switch} from 'react-router-dom';
import {ThemeProvider} from 'styled-components';
import styled from 'styled-components';
import Customizedsolutions from './pages/Customizedsolutions.js';
import Ecommerce from './pages/Ecommerce.js';
import Home from './pages/Home.js';
import Process from './pages/Process.js';
import Plans from './pages/Plans.js';
import Existing from './pages/Existing.js';
import About from './pages/About.js';
import {useSelector,useDispatch} from 'react-redux';

const theme={
  blue1:"#4751DF",
  blue2:"#2D37C5",
  blue3:"#7096FF",
  blue4:"#A2BBFF",
  blue5:"#6C74EB",
  blue6:"#DEE6FF",
  black2:"#2D2B2B",
  grey:"#5D5D5D",
  font1:"Segoe UI",
  font2:"Times New Roman",
  orange1:"#F34437",
  orange2:"#FF3703",
  orange3:"#FF562A",
  orange4:"#FF9D84",
  orange5:"#EEF2FF",
  seriffont:"Times New Roman"
}


const Ap=styled.div`
  max-width:1400px;
  margin:0 auto;
  padding:0 40px;


  @media screen and (max-width : 768px) {
    padding:0 10px;

  }
`;

function App() {


  return (
    <ThemeProvider theme={theme}>
    <Mainheader/>
    <Ap>
      <Router>
        <Switch>
          <Route exact path="/" component={Home}/>
          <Route path="/customizedsolutions" component={Customizedsolutions}/>
          <Route path="/ecommerce" component={Ecommerce}/>
          <Route path="/process" component={Process}/>
          <Route path="/planspricing" component={Plans}/>
          <Route path="/existing" component={Existing}/>
          <Route path="/about" component={About}/>
        </Switch>
      </Router>
    </Ap>
    <Mainfooter/>
    </ThemeProvider>
  );
}

export default App;
