import styled from 'styled-components';


const Prot=styled.div`

  width:450px;
  min-height:535px;
  margin:40px;
  border: solid 5px ${props=>props.theme.blue6};

  padding:10px;
  @media screen and (max-width : 680px) {
    min-height:auto;
    margin:20px 0;
  }
  @media screen and (max-width : 380px) {
    max-width:300px;
    margin:20px 0;
  }


  >div:first-child{
    >p {
      font-size:16px;
      text-indent:15px;
      margin:20px 0 0 0;


    }



  }


  .top-div {

    position:relative;

    >img {
      width:100%;
    }
  }


  .info-div {
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
    text-align:center;
    color:white;
    font-weight:bold;
    font-size:24px;
    >header {


    }

    >span {
      font-weight:bold;
    }

  }




`;





function Processtile(props) {
  return (

    <Prot id={props.id}>
    <div><div className="top-div"><img src={props.pict}/><div className="info-div"><header>{props.head}</header><span>{props.num}</span></div></div><p>{props.para1}</p><p>{props.para2}</p></div>

    </Prot>

  );
}

export default Processtile;
