import styled from 'styled-components';
import Processtile from '../components/Processtile.js';
import process1 from '../images/process1.svg';
import process2 from '../images/process2.svg';
import process3 from '../images/process3.svg';
import process4 from '../images/process4.svg';
import process5 from '../images/process5.svg';
import process6 from '../images/process6.svg';
import process7 from '../images/process7.svg';
import steps from '../images/steps.svg';
import Pageheadingmain from '../components/Pageheadingmain.js';
import Pageheadingsub from '../components/Pageheadingsub.js';
import {HashLink as Linkage} from 'react-router-hash-link';
import {HashRouter as Router, Link, Route} from 'react-router-dom';
import {useEffect} from 'react';

const Pro=styled.div`

  >section:first-child {

    display:flex;
    flex-wrap:wrap;
    align-items:center;
    justify-content:center;
    margin-bottom:0px;

    >div:first-child {
      margin:40px 0 0;

      @media screen and (max-width : 500px) {
        margin:0;

      }

      >div:first-child {
        >div:nth-child(2), >div:nth-child(3){
          @media screen and (max-width: 680px) {
            display:none;
          }
        }
      }

      >div:nth-child(2) {
        min-width:280px;
        display:flex;
        flex-wrap:wrap;
        align-items:flex-start;
        justify-content:center;
        >img {
          max-width:260px;
          margin:20px 0px 0;
          margin-top:20px;


        }

        >div {
          margin-top:20px;

          >header {
            line-height:19px;
            font-size:20px;
            font-weight:bold;
            color:${props=>props.theme.blue1};
          }

          >ol {
            margin-top:5px;


            >a {
                color:${props=>props.theme.orange1};
                font-weight:bold;
                &:visited {
                  color:${props=>props.theme.orange1};
                }

              >li {
                  text-decoration:underline;

              }

            }
          }
        }

      }


    }


    >div:last-child {
      flex:1 1 330px;
      display:flex;
      flex-direction:column;
      align-items:center;
      max-width:500px;
      >span {
        margin:10px 0;
        color:${props=>props.theme.blue1};
        font-weight:bold;
      }
    }
  }

  >section:nth-child(2) {
    display:flex;
    flex-wrap:wrap;
    align-items:center;
    justify-content:center;
    border-top:solid 5px ${props=>props.theme.blue1}
  }

`;


function Process() {
  useEffect(()=>window.scrollTo(0,0),[])
  return (

    <Pro>
      <section>

        <div>
          <div>
            <Pageheadingmain content={"Our Process"}/>
            <Pageheadingsub content={"The Color Blue is All About Trasparency"}/>
            <Pageheadingsub content={"We welcome You to See Our 7-step Process"}/>
          </div>
          <div>
            <img src={steps}/>
            <div>
              <ol>

                <Linkage to="/process#consultation"><li>Consultation</li></Linkage>
                <Linkage to="/process#scope"><li>Define Scope</li></Linkage>
                <Linkage to="/process#map"><li>Site Map/Wireframe</li></Linkage>
                <Linkage to="/process#content"a><li>Content Creation</li></Linkage>
                <Linkage to="/process#visual"><li>Visual Elements</li></Linkage>
                <Linkage to="/process#launching"><li>Launching</li></Linkage>
                <Linkage to="/process#adjustments"><li>Adjustments</li></Linkage>

              </ol>
            </div>
          </div>
        </div>


        <div>
          <span>A Personal Transaction</span>
          <span>Attention to Detail</span>
          <span>State-of-the-Art Technologies and Methods</span>
          <span>Unlimited Options</span>
          <span>No Hidden Costs</span>
        </div>



      </section>
      <section>
      <Processtile id="consultation" head="Consultation" num="1" pict={process1}
      para1={"Once we have a rough idea of what you need, we will arrange a consultation. If you can't come to us, we will come to you, or see you on a video call."}
      para2={"This is when we discuss your business, and how it should be projected online. We will ask you questions about the nature of your business, the demographics of your client base, your competitors, and what the main purpose of your website will be."}
      />

      <Processtile id="scope" head="Define Scope" num="2" pict={process2}
      para1={"Once we have the information we need to get started, we will give you a precise timeline of the project deadline via Gantt chart. "}
      para2={"When you decide to proceed, we will provide you a contract and upon completion, we will reserve your domain name. After this, we can immediately start the next steps, which is the fun part. We keep in touch with you every step of the way, so your website goes up when planned."}
      />

      <Processtile id="map" head="Site Map & Wireframe" num="3" pict={process3}
      para1={"The structure of your website may be simple or very complex. If we can complete this step without your help, we won't bother you. However, working with our designers/developers can give your website the structure that you feel. For example, which products to display more vividly than others. "}
      para2={"We will take the information and data, to shape and design the site according to the best practices of modern web development."}
      />

      <Processtile id="content" head="Content Creation" num="4" pict={process4}
      para1={"Content pertains to the actual words and statements you display on your site. Short, catchy phrases and the words you want to use for the buttons on your start, for example."}
      para2={"This is important for SEO purposes. We will make sure that your website shows up high on the list of search engines like Google, Yahoo, Bing, etc. This process is a delicate blend of your current marketing persona and internet trends."}
      />

      <Processtile id="visual" head="Visual Elements" num="5" pict={process5}
      para1={"The pictures, the colors, the font types, and the combination of all of them on the website. We will do the work of looking at color palettes, going over different component styles, and putting it together to present it to you. You will receive moodboards, or element collages that have different color schemes and styles."}
      para2={"Spend your time going over them, maybe even ask your customers about them. You pick the one you like, and we will start to work on it!"}
      />

      <Processtile id="launching" head="Launching" num="6" pict={process6}
      para1={"We're going to start creating the files and modules that your website will be constructed with. Our designers and developers will work closely to ensure nothing is overlooked, and if we feel like a change is needed, we will recommend it."}
      para2={"We will provide you a link so you can see the website as we are making it. That way, you can see how it looks on different devices and discuss any changes you want to make. Once finished we will launch."}
      />

      <Processtile id="adjustments" head="Adjustments" num="7" pict={process7}
      para1={"Your website is up, it looks great, but you decide a certain area should be located somewhere else. You may have forgotten to give us some important content, or maybe you just want to add something."}
      para2={"We will make any reasonable adjustments for up to 30 days after the official launch date. This is why we recommend that you give us as much information as possible in the beginning of the process. Still, we will do whatever we can to make sure the job gets done right."}
      />

      </section>


    </Pro>

  );


}


export default Process;
