import styled from 'styled-components';
import Button from './Button.js';



let Custo=styled.div`
  display:flex;
  justify-content:center;
  padding:40px 10px;
  flex-direction:row;
  width:100%;
  &:nth-child(even) {
    background:${props=>props.theme.blue6};
  }




  @media screen and (min-width : 981px) {

    &:nth-child(odd){
      flex-direction:row-reverse;

      >div:first-child {
        margin-right:0;
      }

      >div:last-child {
        margin-right:40px;
      }
    }
  }


  @media screen and (max-width : 980px) {
    flex-direction:column;
    align-items:flex-start;
    justify-content:center;
    max-width:500px;
  }



  >div:first-child {
    width:470px;


    margin-right:40px;


    >header {
      font-size:20px;
      font-weight:bold;
      margin:0 0 5px 0;
    }

    >p {
      text-indent:20px;
      margin:0 0 4px 0;
    }

    @media screen and (max-width : 980px) {
      margin-right:0;
    }

    @media screen and (max-width : 530px) {
      width:100%;
    }
  }

  >div:last-child {

    margin-top:37px;
    @media screen and (max-width : 980px) {
      margin-top:20px;
    }

    @media screen and (max-width : 530px) {
      margin-top:5px;
    }


    .image-box {

      display:flex;
      align-items:center;
      width:400px;

      @media screen and (max-width : 530px) {
        width:100%;
      }

      >header {
        margin-left:10px;
        font-weight:bold;

        @media screen and (max-width : 530px) {
          display:none;
        }
      }

      >img {

        width:60px;
        @media screen and (max-width : 530px) {
          display:none;
        }
      }
    }

    .solid-line {
      height:4px;
      width:100%;
      background:black;
      margin:5px 0 2px;

      @media screen and (max-width : 530px) {
        display:none;
      }
    }

    >button {
      margin-top:13px;

    }

  }
`;








function Customizedtile(props) {
  return (
    <Custo>
      <div>
        <header>{props.head}</header>
        <p>{props.para1}</p>
        <p>{props.para2}</p>
      </div>
      <div>
        <div className="image-box"><img src={props.pict}/><header>{props.headtwo}</header></div>
        <div className="solid-line"></div>
        <Button content={props.btntxt} link={props.btnlink}/>
      </div>
    </Custo>
  );
}

export default Customizedtile;
