import styled from 'styled-components';
import {useState} from 'react';
import {useDispatch} from 'react-redux';




const Svcbtn= styled.div`
  padding:10px;
  background:${props=>props.mode=="light"?"white":props.theme.blue1};
  border:solid 2px;
  border-color:${props=>props.mode=="light"?props.theme.orange2:props.theme.blue1};
  color:${props=>props.mode=="light"?props.theme.orange2:"white"};
  font-weight:bold;
  margin:20px;
  width:70px;
  text-align:center;
  cursor:pointer;
`;


const Svc  = styled.div`
  display:flex;
  flex-direction:column;
  align-items:center;


    >span{
      font-size:20px;
      font-weight:bold;

    }



    >div:last-child {

      display:flex;

      justify-content:center;
      flex-wrap:wrap;
    }
`;










function Servicequestion () {
  const [mode,toggleMode]=useState(["light","light","light","light"])
  const dispatch=useDispatch()



  function darkenFirst(){
    console.log("sa")
    toggleMode((last)=>["dark","light","light","light"])
    dispatch({type:'no-ecommerce'});
  }

  function darkenSecond(){
    console.log("sa")
    toggleMode((last)=>["light","dark","light","light"])
    dispatch({type:'services'});
  }

  function darkenThird(){
    console.log("sa")
    toggleMode((last)=>["light","light","dark","light"])
    dispatch({type:'products'});
  }

  function darkenFourth(){
    console.log("sa")
    toggleMode((last)=>["light","light","light","dark"])
    dispatch({type:'both'});
  }

  return (
    <Svc>


      <div onClick={darkenFirst}><Svcbtn mode={mode[0]}>No</Svcbtn></div>
      <span>Yes</span>
      <div>
        <div onClick={darkenSecond}><Svcbtn mode={mode[1]}>Services</Svcbtn></div>
        <div onClick={darkenThird}><Svcbtn mode={mode[2]}>Products</Svcbtn></div>
        <div onClick={darkenFourth}><Svcbtn mode={mode[3]}>Both</Svcbtn></div>
      </div>


    </Svc>
  );
}

export default Servicequestion;
