import styled from 'styled-components';
import Button from './Button.js';
import {useState, useEffect} from 'react';



const Planp = styled.div`
   width:265px;
   box-shadow:-2px 2px 10px 1px ${props=>props.theme.black2};
   display:flex;
   flex-direction:column;
   justify-content:flex-start;
   border-radius:10px;
   min-height:145px;
   cursor:pointer;
   background:${props=>props.mode=="light"?"white":props.theme.blue1};
   >header {

     text-align:center;
     font-size:20px;
     font-weight:bold;
     text-decoration:underline;
     color:white;
     padding:5px 0;
     border-radius:10px 10px 0 0;
     background:${props=>props.mode=="light"?props.theme.orange2:props.theme.blue1};
   }

   >div {

     display:flex;
     justify-content:space-around;
     margin:10px 5px;



     >span {
       text-align:center;
       display:flex;
       flex-direction:column;

       font-size:16px;
       font-weight:bold;
       color:${props=>props.mode=="light"?"black":"white"};

       .divider {
         width:100%;
         height:2px;
         background:${props=>props.mode=="light"?"black":"white"};
       }
     }

     .line {
       background:black;
       height:40px;
       width:1px;
     }


   }


   }

   >div:last-child {
     align-self:center;
     font-weight:bold;
     color:${props=>props.mode=="light"?props.theme.orange2:props.theme.blue1};
   }

`;






function Planpackage(props) {

  const [color, switchColor]=useState("light");




  return (
    <Planp className="pl" mode={props.mode}>
      <header>{props.head}</header>
      <div>
        <span>
          <div>{`${(props.pages)[0]}-${(props.pages)[1]}`}</div>
          <div className="divider"></div>
          <div>Pages</div>
        </span>
        <div className="line"></div>
        <span>
          <div>{"$" + props.hosting + "/yr"}</div>
          <div className="divider"></div>
          <div>Hosting</div>
        </span>
        <div className="line"></div>
        <span>
          <div>{'$'+props.price}</div>
          <div className="divider"></div>
          <div>Page</div>
        </span>
      </div>
      <div>Select</div>
    </Planp>

  );
}

export default Planpackage;
