import styled from 'styled-components';

const Homet= styled.div`
  margin:20px;
  max-width:500px;

  @media screen and (max-width : 500px) {
    margin:20px 0;
  }

  >header{
    font-family: 'Fredoka One', cursive;
    margin:0;
    font-size:20px;
    font-weight:normal;
    color:${props=>props.theme.black1};
    text-align:center;
  }

  >p {
    font-size:16px;
    margin:0 0 5px;
    text-align:center;

  }

  >img {
    width:100%;

  }
`;

function Hometile(props) {
  return (
    <Homet>
      <header>{props.head}</header>
      <p>{props.paragraph}</p>
      <img src={props.picture}/>
    </Homet>
  );
}

export default Hometile;
