import styled from 'styled-components';

const Pagehead = styled.div`

  font-size:24px;
  line-height:24px;
  color:${props=>props.theme.black2};
  font-weight:bold;
`;



function Pageheadingsub(props) {
  return (
    <Pagehead>
      {props.content}
    </Pagehead>
  );
}

export default Pageheadingsub;
