import styled from 'styled-components';
import {useState} from 'react';
import {useDispatch} from 'react-redux';




const Updatebtn= styled.div`
  padding:10px;
  background:${props=>props.mode=="light"?"white":props.theme.blue1};
  border:solid 2px;
  border-color:${props=>props.mode=="light"?props.theme.orange2:props.theme.blue1};
  color:${props=>props.mode=="light"?props.theme.orange2:"white"};
  font-weight:bold;
  width:80px;
  text-align:center;
  cursor:pointer;
`;


const Upd  = styled.div`
  display:flex;
  flex-direction:column;
  align-items:center;

  >div:first-child {

    display:flex;
    justify-content:center;
    flex-wrap: wrap;
    >div {
      margin:20px;

      text-align:center;
    }
  }

  .custom-box {

    display:flex;
    justify-content:center;
    align-items:center;
    flex-wrap:wrap-reverse;

    >div {
      margin:20px;
    }

    >div:last-child {

      display:flex;
      align-items:center;

      >span {
        font-size:20px;
        font-weight:bold;
        opacity:${props=>props.mode=="light"?".4":"1"};
      }
      >input {
        width:30px;
        margin:0 10px;
        pointer-events:${props=>props.mode=="light"?"none":"initial"};

      }
    }
  }



`;










function Updatequestion () {

  const [mode,toggleMode]=useState(()=>["light","light","light","light","light","light"]);
  const dispatch=useDispatch();

  function darken(ind){

    let new_mode=["light","light","light","light","light","light"];
    console.log(new_mode)
    new_mode.splice(ind,1,"dark")
    console.log(new_mode)
    toggleMode(new_mode);
    ind<5?document.getElementById("inp").value="":console.log("hi");

  }

  function sendOne () {
    dispatch({type:'weekly'})
  }

  function sendTwo () {
    dispatch({type:'biweekly'})
  }

  function sendThree () {
    dispatch({type:'monthly'})
  }

  function sendFour () {
    dispatch({type:'yearly'})
  }

  function sendFive () {
    dispatch({type:'never'})
  }

  function sendSix () {
    dispatch({type:'custom'})
  }

  function customUpdates(e){
    dispatch({type:"customUpdate",data:e.target.value})
  }

  return (
    <Upd mode={mode[5]}>

      <div>
        <div>
          <div onClick={()=>darken(0)}><Updatebtn onClick={sendOne} mode={mode[0]}>Weekly</Updatebtn></div>

        </div>

        <div>
          <div onClick={()=>darken(1)}><Updatebtn onClick={sendTwo} mode={mode[1]}>Bi-Weekly</Updatebtn></div>

        </div>

        <div>
          <div onClick={()=>darken(2)}><Updatebtn onClick={sendThree} mode={mode[2]}>Monthly</Updatebtn></div>

        </div>

        <div>
          <div onClick={()=>darken(3)}><Updatebtn onClick={sendFour} mode={mode[3]}>Yearly</Updatebtn></div>

        </div>

        <div>
          <div onClick={()=>darken(4)}><Updatebtn onClick={sendFive} mode={mode[4]}>Never</Updatebtn></div>
          
        </div>
      </div>


      <div className="custom-box">
        <div onClick={()=>darken(5)}><Updatebtn onClick={sendSix} mode={mode[5]}>Custom</Updatebtn></div>
        <div>
          <span>Every</span>
          <input id="inp" type="number" min= "1" max="365" onChange={(e)=>customUpdates(e)} />
          <span>Days</span>
        </div>
      </div>

    </Upd>
  );
}

export default Updatequestion;
