import styled from 'styled-components';
import Button from './Button.js';
import cart from '../images/cart.svg';
import {useState} from 'react';
import {useSelector} from 'react-redux';

const Cart=styled.div`
  display:flex;
  justify-content:space-around;
  flex-direction:column;
  align-items:center;

  >div {
    margin:20px 20px;

    @media screen and (max-width: 500px) {
      margin:20px 0;
    }

  }


  >div:first-child {
    display:grid;
    grid-template-rows:repeat(auto-fit,auto);
    grid-gap:20px;

    border-radius: 20px;
    box-shadow:3px 3px 10px 3px grey;
    padding:20px;
    box-sizing:border-box;
    >div:first-child {

      display:flex;
      flex-wrap:wrap;


      >header {
        font-size:24px;
        font-weight:bold;
        margin-left:5px;
      }
    }

    >div:last-child {
      display:flex;
      flex-direction:column;
      align-items:center;

      >div:first-child {
        display:flex;
        flex-direction:column;
        >span:nth-child(1) {
          color:black;
        }
        >span:nth-child(2) {
          color:${props=>props.color[0]};
        }
        >span:nth-child(3) {
          color:${props=>props.color[1]};
        }
        >span:nth-child(4) {
          color:${props=>props.color[2]};
        }

      }



      >div:last-child {
        margin:20px 0;

      }
    }

  }







  >div:last-child {
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:center;


    >header {
      font-size:30px;
      font-weight:bold;
      line-height:30px;
      margin-bottom:3px;
    }

    >span {
      font-size:24px;
      font-weight:bold;
      line-height:20px;
      color:${props=>props.theme.blue1};
    }

    >button {
      margin:20px 0;

    }

    @media screen and (max-width: 768px) {

      >header {
        font-size:24px;
      }

      >span {
        font-size:18px;
      }
    }

  }

`;





function Cartbox() {
  let form=useSelector((state)=>state.form);

  let color=[
    form.size=="Size required"?"red":"black",
    form.ecommerce=="Ecommerce type required"?"red":"black",
    form.updates=="Update interval required" || form.updates=="Please be exact"?"red":"black"

  ]


  return (
    <Cart color={color}>
      <div>
        <div>
          <img src={cart}/>
          <header>Your Cart</header>
        </div>
        <div>
          <div>
            <span>Domain Name (.com, .net, etc.)</span>
            <span>1. {form.size}</span>
            <span>2. {form.ecommerce}</span>
            <span>3. {form.updates}</span>
          </div>
          <div><Button content={"Start the Process"}/></div>
        </div>
    </div>







      <div>
        <header>Customized Solutions</header>
        <span>Dont' See What You Need?</span>
        <span>We understand.</span>
        <Button content={"Tell Us What You Need"} link={"/customizedsolutions"}/>
      </div>
    </Cart>
  );
}

export default Cartbox;
