import styled from 'styled-components';
import Pageheadingmain from "../components/Pageheadingmain.js";
import Pageheadingsub from "../components/Pageheadingsub.js";
import Spinner from '../components/Spinner.js';
import Button from '../components/Button.js';
import Customizedtile from '../components/Customizedtile.js';
import dollar from '../images/dollar.svg';
import camera from '../images/camera.svg';
import update from '../images/update.svg';
import wrench from '../images/wrench.svg';
import {useEffect} from 'react';

const Cust=styled.div`



  padding:0 40px;
  box-sizing:border-box;

  @media screen and (max-width : 630px) {
    padding:0 10px;
  }


  >section:first-child {

    >div:first-child {
      >h2 {
        margin-bottom:10px;


        @media screen and (max-width : 680px) {
          text-align:center;
        }

      }

      >div:nth-child(2), >div:nth-child(3), >div:nth-child(4)   {
        margin-bottom:5px;


        @media screen and (max-width : 680px) {
          display:none;
        }
      }
    }


      >div:nth-child(2) {
        display:flex;
        justify-content:center;
        margin:40px 0 0 0;
        @media screen and (max-width : 1130px) {
          flex-direction:column;
          align-items:center;
        }
      }
  }

  .consult-box {
    margin:0 9% 0 0;
    display:flex;
    justify-content:center;

    @media screen and (max-width : 1130px) {
      margin:0 0 40px 0;

    }

    @media screen and (max-width : 630px) {
      flex-direction:column;
      align-items:center;
    }
  }




  .consult-info {




    margin-right:20px;
    >header {
      font-size:20px;
      font-weight:bold;
      color:${props=>props.theme.blue1};
    }

    >p {
      color:black;
      margin:0;
      font-weight:bold;
    }

    >ul {
        margin:10px 0;
        padding:0;
        padding-left:20px;
        >li {
          list-style-type:none;
        }
    }

    >span {
      font-weight:bold;
      font-style:italic;
      color:${props=>props.theme.orange2}
    }

    @media screen and (max-width : 680px) {
      text-align:center;
    }

  }

  .consult-form {
    display:flex;
    flex-direction:column;
    align-items:center;

    >button {
      margin-top:20px;
    }

    >textarea {
      height:240px;
      min-width:300px;
      resize:none;
      box-sizing:border-box;
      &:focus {
        outline:none;
        border:solid 3px ${props=>props.theme.orange2};
      }


      @media screen and (max-width : 630px) {
        margin-top:20px;
        min-width:0;
        width:280px;

      }
    }

  }






  >section:last-child{
    padding-top:80px;
    display:flex;
    flex-direction:column;
    align-items:center;
    .last-para {
      max-width:470px;
      padding-top:40px;
      >header{
        font-size:20px;
        font-weight:bold;
        font-family:Segoe UI;
      }

      >p {
        text-indent:20px;
      }
    }
  }


`;


function Customizedsolutions() {

  useEffect(()=>window.scrollTo(0,0))
  return (
    <Cust>

      <section>
        <div>
          <Pageheadingmain content="Customized Solutions"/>
          <Pageheadingsub content="Your needs are unique and constantly evolving."/>
          <Pageheadingsub content="The Color Blue is different from the competition."/>
          <Pageheadingsub content="We specialize in catering to your business platform."/>
        </div>


        <div>

          <div className="consult-box">
            <div className="consult-info">
              <header>Tell Us Exactly What You Need</header>
              <p>Please be as detailed as possible.</p>
              <p>Keep in mind we offer the following customized services for our clients:</p>
              <ul>
                <li>- E-Commerce</li>
                <li>- Digital Photography</li>
                <li>- Site/Graphic Updates</li>
                <li>- Site Remodels</li>
                <li>- Extra Large Websites (31+ pages)</li>
              </ul>
              <span>We will review your needs and respond within 24-48 hours.</span>
            </div>
            <div className="consult-form">
              <textarea>For recruiters/employers: This form doesn't actually work :)
              </textarea>
              <Button content="Request Consultation"/>
            </div>
          </div>

          <Spinner/>

        </div>


      </section>


      <section>
        <Customizedtile head={"E-Commerce"} pict={dollar}  btntxt={"Our E-Commerce Solutions"} btnlink={'/ecommerce'}
          headtwo={"Grow Your Customer Base by Selling Your Products and/or Services Online"}
          para1={"E-commerce is necessary if you want to grow your client base beyond your physical location. Today, many businesses do a major chunk of their business online."}
          para2={"We welcome you to check them out and read why e-commerce can grow your commercial ecosystem."}
        />
        <Customizedtile head={"Digital Photography"} pict={camera}  btntxt={"Great Photography"} btnlink={'/'}
          headtwo={"Bring Your Website(s) to Life Today With the Magic of Photography!"}
          para1={"A picture is worth a thousand words. A website with real photos will give your business a more personal feel."}
          para2={"We can take care of that part ourselves. We can schedule a visit to your business location and take the photos that will look the best for your site and brand."}
        />
        <Customizedtile head={"Graphic/Site Updates"} pict={update}  btntxt={"Updates We Perform"} btnlink={'/'}
          headtwo={"Keep Your Website Looking Fresh With Regular Updates/New Trends"}
          para1={"In this day and age, having a website that reflects the current design trends is extremely crucial."}
          para2={"That still doesn't mean you have to lift a finger. You can schedule updates that we will execute on time, or contact us when you feel something needs to be displayed online."}
        />
        <Customizedtile head={"Site Remodels"} pict={wrench}  btntxt={"Site Remodels"} btnlink={'/existing'}
          headtwo={"Have a Website Already? No Problem. Let Us Overhaul it !"}
          para1={" If you don't need an entirely new website, that's not a problem. We can take your existing site and recreate it with new or the same content."}
          para2={"We offer solutions for remodels that may be best fit for you. If you don't see what you like, send us a message above!"}
        />

        <div className="last-para">
          <header>Extra-Large Websites (31+ pages)</header>
          <p>Larger websites tend to be more complex and usually require
            much more attention, time, and planning to create. For this
            reason, we request that you send us a message that describes
            your website.
          </p>
        </div>

      </section>
    </Cust>

  );
}

export default Customizedsolutions;
