import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {createStore} from 'redux';
import {Provider} from 'react-redux';

const initialState={
    form:{
      size:"Size required",
      ecommerce:"Ecommerce type required",
      updates:"Update interval required"
    }
}

function formReducer (state=initialState, action,data) {
  let mydata=action.data
  switch(action.type) {
    case 'size-small':
    return {
      ...state,
        form:{
          ...state.form,
          size:"Small website (1-5 pages)"
        }
    }
    case 'size-medium':
    return {
      ...state,
        form:{
          ...state.form,
          size:"Medium website (6-15 pages)"
        }
    }
    case 'size-large':
    return {
      ...state,
        form:{
          ...state.form,
          size:"Large website (16-30 pages)"
        }
    }
    case 'no-ecommerce':
    return {
      ...state,
        form:{
          ...state.form,
          ecommerce:"Selling nothing"
        }
    }
    case 'products':
    return {
      ...state,
        form:{
          ...state.form,
          ecommerce:"Selling Products"
        }
    }
    case 'services':
    return {
      ...state,
        form:{
          ...state.form,
          ecommerce:"Selling Services"
        }
    }
    case 'both':
    return {
      ...state,
        form:{
          ...state.form,
          ecommerce:"Selling Products and Services"
        }
    }
    case 'weekly':
    return {
      ...state,
        form:{
          ...state.form,
          updates:"Weekly"
        }
    }
    case 'biweekly':
    return {
      ...state,
        form:{
          ...state.form,
          updates:"Bi-Weekly"
        }
    }
    case 'monthly':
    return {
      ...state,
        form:{
          ...state.form,
          updates:"Monthly"
        }
    }
    case 'yearly':
    return {
      ...state,
        form:{
          ...state.form,
          updates:"Yearly"
        }
    }
    case 'never':
    return {
      ...state,
        form:{
          ...state.form,
          updates:"Never"
        }
    }
    case 'custom':
    return {
      ...state,
        form:{
          ...state.form,
          updates:"Please be exact"
        }
    }
    case 'customUpdate':
    return {
      ...state,
        form:{
          ...state.form,
          updates:`Update every ${mydata} days`
        }
    }



    default:
      return state;





}

}

let store=createStore(formReducer);





ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}><App /></Provider>
  </React.StrictMode>,
  document.getElementById('root')
);
