import styled from 'styled-components';
import Inputform from '../components/Inputform.js';
import Pageheadingmain from '../components/Pageheadingmain.js';
import {useEffect} from 'react';

const Ab=styled.div`
  display:grid;
  grid-row-gap:40px;
  justify-items:center;
  >div:nth-child(2) {

    display:grid;
    grid-template-columns:1fr 1fr;
    grid-gap:40px;
    justify-items:center;

    @media screen and (max-width: 768px) {
        grid-template-columns:1fr;
    }

    >div:first-child {
      max-width:500px;
        header {
          font-size:20px;
          font-weight:bold;

        }
        p {
          margin:10px 0;
        }

      }

      >div:last-child {
        max-width:500px;
        text-align:center;
        >header {
          font-size:20px;
          font-weight:bold;
          margin-bottom:10px;
        }

        >div {
          background:${props=>props.theme.blue1};
          width:100%;
          padding:20px 0;
          color:white;
        }

      }



  }

  >div:last-child {
    width:400px;

    @media screen and (max-width: 430px){
      width:100%;
    }

    >div {
      width:100%;
    }
  }



`;



function About () {
  useEffect(()=>window.scrollTo(0,0))
  return (
    <Ab>
      <Pageheadingmain content={"About/Contact"}/>
      <div>

        <div>
          <div>
            <header>Business Model</header>
            <p>
              The Blue Color was founded in Chicago. We are a decentralized organization that
              operates throughout the United States. We provide web services for clients all
              over the world. Still, most of our infrastructure is located back in the Windy City.
            </p>
          </div>
          <div>
            <header>Our Clients</header>
            <p>
              We are proud supporters of small businesses in the United States. We believe that a
              website should transform and scale your business' footprint in your industry. It's no
              surprise that the majority of our clients are owners of small businesses.
            </p>
          </div>
        </div>


        <div>
          <header>Why Blue?</header>
          <div>#340A8A</div>
          <p>
            Blue is the most rare color that occurs in nature. It only made sense that our company
            be named after it. Our unique style of operation sets us apart from the competition. With us,
            you're not just a number.
          </p>
        </div>


      </div>

      <div><Inputform/></div>
    </Ab>
  );

}

export default About;
